@charset "UTF-8";
html {
  height: 100%;
}

body,
html {
  margin: 0;
  font-family: "Arial", "Helvetica", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  /* убирает пробелы, не дает переносить текст на другие строки */
  clip-path: inset(100%);
  /* определяет область отображения текста по шаблону. совместимость средняя */
  clip: rect(0 0 0 0);
  /* определяет область отображения текста по прямоугольнику. совместимость высокая */
  overflow: hidden;
  /* определяет что делать с элементами, которые не вмещаются в блок: показать/скрыть/скролл. можно применять к блокам, флексам и гридам */
}

a, ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

.main-page {
  position: relative;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 19px;
  line-height: 23px;
  background: #ece8dd url("../../../src/images/fone.gif") left top repeat;
}

.hero__container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.main-page__hero-image {
  position: relative;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
  z-index: 2;
}

.hero {
  max-width: 1500px;
}

.hero__image {
  width: 100%;
  height: auto;
}

.hero__header {
  position: relative;
  margin: 0 auto;
  z-index: 2;
}

.main-page-header {
  max-width: 1300px;
  display: flex;
  padding: 25px 75px;
  align-items: center;
  justify-content: space-between;
}

.user-block__avatar {
  padding-right: 25px;
}

.user-block {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.user-block__item {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  color: #e3e319;
}

.main-page__film-card {
  position: relative;
  margin: 0 auto;
  z-index: 2;
}

.film-card {
  max-width: 1300px;
  padding: 25px 75px;
  display: flex;
}

.film-card__title {
  font-size: 32px;
  line-height: 45px;
  color: #ffff1b;
}

.film-card__meta {
  font-size: 17px;
  line-height: 20px;
  color: #ffff1b;
}

.main-page__content {
  width: 1200px;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
}

.main-page__footer {
  margin-top: auto;
  max-width: 1200px;
  background: gray url("../../../src/images/content-fone.jpg") -300px 330px repeat;
}

.ass2 {
  background-color: #ffffff;
}

body {
  background-color: #ffffff;
}